@font-face {
    font-family: "Lato";
    src: url("../fonts/LatoRegular/LatoRegular.eot");
    src: url("../fonts/LatoRegular/LatoRegular.eot?#iefix")format("embedded-opentype"),
        url("../fonts/LatoRegular/LatoRegular.woff") format("woff"),
        url("../fonts/LatoRegular/LatoRegular.ttf") format("truetype");
    font-style: normal;
    font-weight: 400;
}

/* font-family: "LatoLight"; */
@font-face {
    font-family: "Lato";
    src: url("../fonts/LatoLight/LatoLight.eot");
    src: url("../fonts/LatoLight/LatoLight.eot?#iefix")format("embedded-opentype"),
        url("../fonts/LatoLight/LatoLight.woff") format("woff"),
        url("../fonts/LatoLight/LatoLight.ttf") format("truetype");
    font-style: normal;
    font-weight: 300;
}

/* font-family: "Lato Medium"; */
@font-face {
    font-family: 'Lato';
    src: url("../fonts/Lato-Medium/Lato-Medium.eot");
    src: url("../fonts/Lato-Medium/Lato-Medium.woff") format("woff"), 
        url("../fonts/Lato-Medium/Lato-Medium.ttf") format("truetype"), 
        url("../fonts/Lato-Medium/Lato-Medium.svg") format("svg");
    font-weight: 500;
    font-style: normal; 
}

@font-face {
    font-family: 'Lato';
    src: url('../fonts/Lato-Semibold/Lato-Semibold.eot');
    src: url('../fonts/Lato-Semibold/Lato-Semibold.woff2') format('woff2'),
        url('../fonts/Lato-Semibold/Lato-Semibold.woff') format('woff'),
        url('../fonts/Lato-Semibold/Lato-Semibold.ttf') format('truetype'),
        url('../fonts/Lato-Semibold/Lato-Semibold.svg#Lato-Semibold') format('svg'),
        url('../fonts/Lato-Semibold/Lato-Semibold.eot?#iefix') format('embedded-opentype');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'lato';
    src: url('../fonts/LatoBold/latobold-webfont.eot');
    src: url('../fonts/LatoBold/latobold-webfont.eot?#iefix') format('embedded-opentype'),
        url('../fonts/LatoBold/latobold-webfont.woff2') format('woff2'),
        url('../fonts/LatoBold/latobold-webfont.woff') format('woff'),
        url('../fonts/LatoBold/latobold-webfont.ttf') format('truetype'),
        url('../fonts/LatoBold/latobold-webfont.svg#latobold') format('svg');
    font-weight: 700;
    font-style: normal;

}
