@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
}

body {
    background: #fff;
    font-family: "Lato";
    font-weight: 400;
    font-size: 14px;
}

input {
    font-family: "Lato";   
    font-size: 18px;
    letter-spacing: 1.2px;
}

button[type=submit] {
    background: transparent;
    border: none;
}

h2, .h2 {
    color: $black;
    font-size: 18px;
    font-weight: 900;
    line-height: 22px;
    letter-spacing: 0.75px;
}

strong {
    font-weight: 700;
}

.container {
    margin: 0 auto;
    padding-bottom: 45px;
    padding-left: 44px;
    padding-right: 44px;
    width: 1440px;
}

.header {
    align-items: center;
    display: flex;
    padding: 24px 0;
}

.header__logo {
    margin-right: 290px;
}

.logo-svg {
    height: 30px;
    width: 108px;
}

.header__form {
    margin-right: 36px;
}

.form {
    position: relative;
    width: 556px;
}

.form__input {
    background: #F8F8F8;
    border-radius: 24px;
    border: none;
    padding: 13px 50px 13px 24px;
    width: 100%;
}

.form__btn {
    position: absolute;
    right: 7px;
    top: 4px;
}

.btn-svg {
    height: 36px;
    width: 36px;
}

.mic {
    border-radius: 50%;
    margin-right: 187px;
}

.mic-svg {
    height: 48px;
    width: 48px;
}

.header__right {
    align-items: center;
    display: flex;
}

.apps-material {
    cursor: pointer;
    height: 16px;
    width: 16px;
}

.notify {
    cursor: pointer;
    height: 18px;
    margin-left: 24px;
    width: 15px;
}

.avatar {
    cursor: pointer;
    height: 48px;
    margin-left: 24px;
    width: 48px;
}

.menu {
    box-shadow: 0px 1px 2px rgba(221, 221, 221, 0.5);
    display: flex;
    justify-content: center;
    
    &__item {
        font-weight: 500;
        font-size: 14px;
        
        + .menu__item {
            margin-left: 25px;
        }
    }
    
    a {
        border-bottom: 5px solid transparent;
        color: #4E4E4E;
        display: block;
        padding: 15px 5px;
        text-decoration: none;
        transition: all .3s ease;
        
        &:hover, &.active {
            border-bottom-color: $blue;
        }
    }
}

.content {
    display: flex;
    padding: 44px 0 0 0;
}

.content__item {
    
    + .content__item {
        margin-left: 24px;
    }
}

.content__box {
    background: #FFFFFF;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.138162);
    border-radius: 4px;
    min-height: 260px;
    padding: 24px 24px 16px 24px;
    position: relative;
    width: 320px;
    
    + .content__box {
        margin-top: 22px;
    }
    
    .h2 {
        margin-bottom: 20px;        
    }
    
    .blue {
        font-size: 15px;
        line-height: 21px;
    }
    
    .green {
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.8px;
        margin-left: 17px;
        text-decoration: none;
    }
}

.p-1 {
    font-weight: 300;
    font-size: 13px;
    line-height: 21px;
}

.p-2 {
    font-weight: 300;
    font-size: 15px;
    line-height: 23px;
}

.blue {
    color: $blue-1;
}

.green {
    color: $green;
}

.dots {
    display: flex;
    justify-content: center;
    margin: 22px 0 0 0;
    
    &__item {
        background: #EAEAEA;
        border-radius: 50%;
        height: 6px;
        width: 6px;
        
        &:first-child {
            background: #4E4E4E;
        }
        
        + .dots__item {
            margin-left: 3px;
        }
    }
}

.wiki-logo {
    bottom: 23px;
    display: block;
    left: 24px;
    position: absolute;
}

.images {
    background: #FFFFFF;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.138162);
    border-radius: 4px;
    padding: 0 0 11px 0;
    position: relative;
    
    .close-icon {
        cursor: pointer;
        left: 20px;
        position: absolute;
        top: 16px;
        z-index: 1;
    }
}

.close-icon {
    fill: #fff;
    height: 15px;
    width: 15px;
}

.img {
    display: block;
}

.images__btns {
    display: flex;
    margin: 0 0 17px 0;
    padding: 0 24px;
    
    > li {
        font-size: 14px;
        padding: 18px 0 16px 0;
        
        + li {
            margin-left: 31px;
        }
        
        &:last-child {
            margin-left: auto;
        }
    }
    
    a {
        align-items: center;
        color: $black;
        display: flex;
        text-decoration: none;
        
        svg {
            margin-right: 7px;
        }
    }
}

.visit-icon {
    height: 15px;
    width: 15px;
}

.save-icon {
    height: 14px;
    width: 11px;
}

.share-icon {
    height: 15px;
    width: 14px;
}

.arrow-next {
    height: 12px;
    margin-left: 8px;
    width: 12px;
}

.p-2 {
    color: #4E4E4E;
    font-size: 14px;
    line-height: 17px;
    margin: 0 0 5px 0;
    padding: 0 24px;
}

.p-3 {
    color: $blue;
    font-size: 16px;
    line-height: 23px;
    margin: 0 0 7px 0;
    padding: 0 24px;
}

.p-4 {
    color: #4E4E4E;
    font-size: 13px;
    line-height: 23px;
    padding: 0 24px;
}

.maps {
    display: block;
    margin-bottom: 15px;
}

.weather {
    display: block;
}

.footer {
    display: flex;
    justify-content: space-between;
    margin: 24px 0 0 0;

    &__item {
        border-radius: 4px;        
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.138162);
        width: 320px;
        
        &:last-child {
            width: 664px;
        }
        
        .h2 {
            align-items: center;
            color: #FFFFFF;
            display: flex;
            margin: 0 0 15px 0;
            
            .flight-icon {
                margin-right: 16px;
            }
            
            .hotel-icon {
                margin-right: 14px;
            }
        }
    }
    
    &__box {
        border-radius: 4px;        
        color: #FFFFFF;
        font-size: 15px;
        font-weight: 500;
        line-height: 23px;
        overflow: hidden;
        padding: 23px 23px 10px 24px;
    }
}

.footer-blue {
    background: $blue;
}

.footer-black {
    background: $black;
}

.flight-icon {
    height: 22px;
    width: 21px;
}

.p-5 {
    font-weight: 500;
    font-size: 15px;
    line-height: 23px;
}

.hotel-icon {
    height: 15px;
    width: 22px;
}

.translate {
    display: flex;
    position: relative;
    
    &::after {
        background: #EAEAEA;
        bottom: -13px;
        content: '';
        left: calc(50% - 1px);
        position: absolute;
        top: 0;
        width: 1px;
    }
}

.translate__item {
    padding: 20px 24px 5px 24px;
    width: 50%;
}

.translate__row {
    display: flex;
    justify-content: space-between;
    margin: 0 0 6px 0;
}

.dropdown-icon {
    fill: #4E4E4E;
    height: 4px;
    width: 8px;
}

.translate__lang {
    align-items: center;
    cursor: pointer;
    display: flex;
    
    .dropdown-icon {
        margin-left: 5px;
    }
}

.translate__btn {
    align-items: center;
    display: flex;
    
    > li + li {
        margin-left: 12px;
    }
}

.speak-icon,
.listen-icon,
.changeactions-icon {
    cursor: pointer;
    height: 32px;
    width: 32px;
}

.translate__word {
    font-size: 36px;
    display: block;
}

.translate__descript {
    bottom: -8px;
    color: #4E4E4E;
    font-size: 10px;
    position: absolute;
    right: 10px;
}

